                <p class="copy_message" style="display: none">
                  SUCCESSFULLY COPY
                </p>
                <button
                  type="button"
                  class="btn btn-success d-block"
                  style="float: left; display: none"
                  @click="copyToClipboard ('.working_code')"
                >
                  COPY CODE
                </button>
                <button
                  type="button"
                  class="btn btn-danger"
                  data-dismiss="modal"
                  @click="simpleDialog = false"
                >
                  Close
                </button>
              </div>
            </div>
          </div>

        </div>
      <!-- </v-easy-dialog> -->
    </section>
    <hr id="scroll_tobottom" />
  </div>
</template>

<script>
import VEasyDialog from "v-easy-dialog";
global.jQuery = require('jquery');
var $ = global.jQuery;
window.$ = $;
export default {
  name: "Syntax",
  components: {
    VEasyDialog,
  },

  props: {
    msg: String,
  },

  data: () => {
    return {
      active_el: "fyres_syntax",
      MainButton: "",
      tradingsymbolShow: false,
      tradingsymbol: "",
      quantitypercentage: "",
      quantity: "",
      Transaction: "",
      OrderType: "",
      Fund: "",
      LEVERAGE: "",
      Producttype: "",
      simpleDialog: false,
      TRPpercentage: "",
      LTPpercentage: "",
      TPpercentage: "",
      SLpercentage:"",
      LTP: "",
      SL: "",
      TP: "",
      TRP:'',
      auto_mation:'',
      NEARBY:'',
NEARBYCAL:'',
cal_plus_minus :'',
PER_LOT_SIZE:''
    };
  },
  methods: {
    ActiveTab(liname) {
        this.tradingsymbol="";
      this.tradingsymbol= "";
      this.quantitypercentage= "";
      this.quantity= "";
      this.Transaction= "";
      this.OrderType= "";
      this.Fund= "";
      this.LEVERAGE= "";
      this.Producttype= "";
      this.simpleDialog= false;
      this.TRPpercentage= "";
      this.LTPpercentage= "";
      this.TPpercentage= "";
      this.SLpercentage="";
      this.LTP= "";
      this.SL= "";
      this.TP= "";
      this.MainButton='';
      this.active_el = liname;
    },
    
    MainVaritey(name) {
       this.tradingsymbol="";
      this.tradingsymbol= "";
      this.quantitypercentage= "";
      this.quantity= "";
      this.Transaction= "";
      this.OrderType= "";
      this.Fund= "";
      this.LEVERAGE= "";
      this.Producttype= "";
      this.simpleDialog= false;
      this.TRPpercentage= "";
      this.LTPpercentage= "";
      this.TPpercentage= "";
      this.SLpercentage="";
      this.LTP= "";
      this.SL= "";
      this.TP= "";
      this.MainButton = name;
      if (name === "REGULAR" || name === "CO" || name === "BO" || name==='CANCEL') {
        this.tradingsymbolShow = true;
      } else {
        this.tradingsymbolShow = false;
      }
    },
    TransactionType(name) {
      this.Transaction = name;
    },
    OrderTypeChange(name) {
      this.OrderType = name;
    },
    FundType(name) {
      this.Fund = name;
    },
    ProducttypeC(name) {
      this.Producttype = name;
    },

    QuantityPercentageChange(event) {
      if (event.target.checked) {
        this.quantitypercentage = "%";
      } else {
        this.quantitypercentage = "";
      }
    },

    TRPpercentageChange(event) {
      if (event.target.checked) {
        this.TRPpercentage = "%";
      } else {
        this.TRPpercentage = "";
      }
    },
    LTPpercentageChange(event) {
      if (event.target.checked) {
        this.LTPpercentage = "%";
      } else {
        this.LTPpercentage = "";
      }
    },
    TPpercentageChange(event) {
      if (event.target.checked) {
        this.TPpercentage = "%";
      } else {
        this.TPpercentage = "";
      }
    },

     SLpercentageChange(event) {
      if (event.target.checked) {
        this.SLpercentage = "%";
      } else {
        this.SLpercentage = "";
      }
    },

    ShowModal() {
      this.simpleDialog = true;
    },

    copyToClipboard(element) {
var $temp = $("<textarea>");
                var brRegex = /<br\s*[\\]?>/gi;
                $("body").append($temp);
                $temp.val($(element).html().replace(brRegex, "\r\n")).select();
                document.execCommand("copy");
                $temp.remove();
                $('.copy_message').html('SUCCESSFULLY COPY').css({
                    "display": "block",
                    "position": "absolute",
                    "left": "21px",
                    "margin-top": "-3px",
                    "color": "#00a000"
                });}
  },
};
</script>

